.AnswerScreen {
    top: 56px;
    position: absolute;
    display: block;
    width: 100%;
    margin: auto;
    height: 100%;
    overflow: scroll;
    text-align: center;
    background-color: #F0F0F0;
}

.Flags {
   width: 138px;
   height: 90px;
   padding: 4px 2px 2px 2px;
}

.PokerCard {
   width: 103px;
   padding: 4px 2px 2px 2px;
}

.Shapes {
   width: 110px;
   padding: 4px 2px 2px 2px;
}

.Emojis {
   font-size: 5rem;
}

.Numbers {
   font-size: 5rem;
    display: inline-block;
    padding: 10px;
}

.EvenItem {
   color: purple;
}

.OddItem {
   color: navy;
}

.AnswerScreenItem {
    cursor:pointer
 }

 .blink_me {
   animation: blinker 0.3s linear infinite;
 }
 
 .ItemFailed {
    background-color: red;
    pointer-events: none;
 }

 .ItemNext {
    animation: blinker 0.5s linear infinite;
    background-color: lawngreen;
    pointer-events: none;
 }

 @keyframes blinker {
   50% {
     opacity: 0;
   }
 }

 .ItemsLeft {
    color: red;
    bottom: 10px;
    position: fixed;
    width: inherit;
    font-size: 1.2rem;
 }

 .ItemsList {
   width: 80%;
   margin: auto;
 }


 @media only screen and (max-width: 1023px) {

   .ItemsList {
      width: 100%;
      margin: auto;
    }
   

   .Flags {
      width: 46px;
      height: 30px;
      padding: 2px 2px 8px 2px;
   }

   .PokerCard {
      width: 46px;
      padding: 2px 2px 8px 2px;
   }

   .Shapes {
      width: 46px;
      padding: 2px 2px 4px 2px;
   }

   .Emojis {
      font-size: 3rem;
   }

   .Numbers {
      font-size: 2rem;
      display: inline-block;
      padding: 8px;
   }
}